
import Box from 'vux/src/components/box/index.vue'

import bheader from "@/components/header/header-b";
import circleTitle from './../../../components/circleTitle/circleTitle'
export default {
name: "joinPartner",
data() {
return {
userInfo:localStorage.getItem('userInfo') && JSON.parse(localStorage.getItem('userInfo')) || {},
arr:[{'title':'总服务商','cont1':'体系:全体系分销利润100%)','cont2':'分润：万分之23+2.5元/每笔','cont3':'交易量：循环累计制','right':'晋级考核','right2':'联系客服'},
{'title':'钻石会员（直系3人以上）','cont1':'体系：A-B-C-D(三级分销利润80%,15%,5%)','cont2':'分润：万分之20+2元/每笔','cont3':'交易量：循环累计制','right':'晋级考核','right2':'交易量累计5000万'},
{'title':'铂金会员（直系3人以上）','cont1':'体系：A-B-C-D(三级分销利润80%,15%,5%)','cont2':'分润：万分之15+1元/每笔','cont3':'交易量：循环累计制','right':'晋级考核','right2':'交易量累计500万'},
{'title':'黄金会员','cont1':'体系：A-B-C-D(三级分销利润80%,15%,5%)','cont2':'分润：万分之13','right':'晋级考核','cont3':'交易量：循环累计制','right2':'直系1人'},
{'title':'普通会员','cont1':'体系：无','cont2':'分润:无','cont3':'','right':'晋级考核','right2':'无'}]
}

},
components: {
bheader, circleTitle, Box
},
created() {

},
mounted() {

},
methods: {
pay(type){
const _this = this;
this.$vux.confirm.show({
title: '确认支付？',
onCancel () {
},
onConfirm () {

let payload = {type,userId:localStorage.getItem('Token'),phone:_this.userInfo.phone};

_this.$router.push({name:'payWeb',query:{payload,link:'api/Partner/payMoney',type:'join'}});
/* _this.$http.post('/api/Partner/pay_partner','data='+JSON.stringify({type}))
.then(res=>{
if(res.data.code==1){
console.log(res.data);
_this.$router.push({name:'rate'});
}else{
mui.toast(res.data.msg)
}
}) */
}
})
}
}
}



